<template>
  <div style="margin-left: 90px">
    <el-table :data="tableData" style="width: 100%;margin-bottom: 20px;" border row-key="id">
      <el-table-column prop="date" label="" sortable width="180"></el-table-column>
      <el-table-column prop="name" label="" sortable width="180"></el-table-column>
      <el-table-column prop="address" label=""></el-table-column>
    </el-table>
    
    <el-table :data="tableData1" style="width: 100%" row-key="id" border lazy :load="load">
      <el-table-column prop="date" label="" width="180"></el-table-column>
      <el-table-column prop="name" label="" width="180"></el-table-column>
      <el-table-column prop="address" label=""></el-table-column>
    </el-table>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        tableData: [{
          id: 1,
          date: '2016-05-02',
          name: '',
          address: ' 1518 '
        }, {
          id: 2,
          date: '2016-05-04',
          name: '',
          address: ' 1517 '
        }, {
          id: 3,
          date: '2016-05-01',
          name: '',
          address: ' 1519 ',
          children: [{
            id: 31,
            date: '2016-05-01',
            name: '',
            address: ' 1519 '
          }, {
            id: 32,
            date: '2016-05-01',
            name: '',
            address: ' 1519 '
          }]
        }, {
          id: 4,
          date: '2016-05-03',
          name: '',
          address: ' 1516 '
        }],
        tableData1: [{
          id: 1,
          date: '2016-05-02',
          name: '',
          address: ' 1518 '
        }, {
          id: 2,
          date: '2016-05-04',
          name: '',
          address: ' 1517 '
        }, {
          id: 3,
          date: '2016-05-01',
          name: '',
          address: ' 1519 ',
          hasChildren: true
        }, {
          id: 4,
          date: '2016-05-03',
          name: '',
          address: ' 1516 '
        }]
      }
    },
    methods: {
      load(tree, treeNode, resolve) {
        resolve([
          {
            id: 31,
            date: '2016-05-01',
            name: '',
            address: ' 1519 '
          }, {
            id: 32,
            date: '2016-05-01',
            name: '',
            address: ' 1519 '
          }
        ])
      }
    },
  }
</script>



